      </div>

    </div>
    <div class="recommended-guides">
      <h3 class="title">
        <span></span>
        <a href="" class="more-link fr">
          <svg>
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
          </svg>
        </a>
      </h3>
      <div class="guides-wrapper  slider-wrapper" >
        <HoriSlider :spaceBetween="45" class="guides-list clearfix" ref="guideRecom">
          <div class="guide fl">
            <div class="guide-img">
              <img src="../assets/img/home_poist.jpg" alt="">
            </div>
            <div class="guide-txt">
              <div class="guide-intro"><b></b>2002</div>
              <div class="guide-evalu clearfix">
                <div class="star fl">
                  <EvaluateStar :code="4.5"></EvaluateStar>
                </div>
                <div class="num fl">100</div>
                <div class="method fl">2</div>
              </div>
              <button @click="showDialog" class="guide-order">Ta</button>
            </div>
          </div>
          <div class="guide fl">
            <div class="guide-img">
              <img src="../assets/img/home_poist.jpg" alt="">
            </div>
            <div class="guide-txt">
              <div class="guide-intro"><b></b>2002</div>
              <div class="guide-evalu clearfix">
                <div class="star fl">
                  <EvaluateStar :code="3"></EvaluateStar>
                </div>
                <div class="num fl">100</div>
                <div class="method fl">2</div>
              </div>
              <button class="guide-order">Ta</button>
            </div>
          </div>
        </HoriSlider>

      </div>
    </div>
      <!--<cube-button @click="showDialog">show dialog</cube-button>-->
    <div class="footer">~<br/></div>
    <!--<alert-tip v-if="showAlert" :showHide="showAlert" @closeTip="closeTip" :alertText="alertText"></alert-tip>-->
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapState, mapMutations} from 'vuex'
  import * as types from '../store/mutation-types'
  import {homeData, userLogin} from '../http/getDate'
  import HoriSlider from '../components/HoriSlider.vue'
  import Slider from '../components/newSlider.vue'
  import EvaluateStar from '../components/EvaluateStar.vue'
  import AlertTip from '../components/alertTip.vue'
  import Swiper from '../components/Swiper.vue'
  import Banner from '../components/Banner.vue'
  export default {
    name: 'Home',
    data() {
      return {
        // baseUrl: 'http://www.youdingsoft.com',
        bannerSlider: [],
        slider: [],
        hotArea:  [],
        hotLine: [],
        guideRecom: [],
        showAlert: false,
        alertText: '',
        items: [
        ],
        swiperid: '',
        loop: true,
        autoPlay: true,
        interval: 4000,
        threshold: 0.3,
        speed: 400,
        allowVertical: false,
        initialIndex: 1,
        dotsSlot: false,
        addItem3: false
      }
    },
    components: {
      HoriSlider,
      Slider,
      EvaluateStar,
      AlertTip,
      Swiper,
      Banner
    },
    computed: {
      ...mapState([
        'basePath','location','userInfo', 'baseOrder'
      ]),
    },
    mounted() {
      console.log(WeixinJSBridge)
      //
      this.initHome();
      var _this=this
      setTimeout(() => {
        _this.$refs.silde.refresh()
      }, 1000)
      // this.$refs.slide.refresh()
    },

    methods: {
      ...mapMutations(['GET_USERINFO','RECORD_ADDRESS' ,'BASE_ORDER']),

      async initHome() {
        await userLogin('15814731314', '0123456').then(res=> {
          console.log(res)
        })
        await homeData().then(res => {
          console.log(res);
          console.log(this.basePath);

          this.GET_USERINFO({phone: this.basePath,name: ''});
          this.RECORD_ADDRESS({latitude: 2222, longitude: 5555})
          let resp = res;
          this.items = resp[1]   //banner
          for(var i of this.items) {
            // console.log(i)
            i.img = this.basePath + i.img
          }


          this.hotArea = resp[3]; //

          setTimeout(() => {
            this.$refs.hotArea.init();
          }, 20)

          this.slider = resp[4]; //
          setTimeout(() => {
            this.$refs.hotSpots.init();
          }, 20)


          this.hotLine = resp[5];
          // console.log(resp[5])
        }).catch(err => {

          console.log(err)
        });
        console.log('')
      },
      goSearch() {
        this.$router.push({path: '/search'})
      },
      changePage(current) {
        // console.log(':' + current)
      },
      clickPage(item, index) {
        // console.log(item, index)
      },
      closeTip() {
        this.showAlert = false;
      },
      show() {
        console.log('......');
      },

      showDialog() {
        console.log('xxxx')
        this.$createDialog({
          type: 'alert',
          title: 'Alert',
          content: 'dialog content'
        }).show()
      },
      getImgUrl(url, mode) {
        let baseUrl = this.basePath;
        let dUrl = url.split('/')[1];
        switch (mode) {
          case 'small':
            return baseUrl + 'fileUploadsmall/' + dUrl;
          case 'middle':
            return baseUrl + 'fileUploadmedium/' + dUrl;
          case 'orgin':
            return baseUrl + 'fileUpload/' + dUrl;
          default:
            return baseUrl + 'fileUploadsmall/' + dUrl
        }
      }
    }
  }
</script>

